import { ActionTypes } from "@mui/base";
import React, { useEffect, useReducer } from "react";
import Loader from "./Loader";
import Movies from "./Movies";
import SearchIcon from "@mui/icons-material/Search";
import MicIcon from "@mui/icons-material/Mic";
import IconButton from "@mui/material/IconButton";
import ModalVoiceBox from "./ModalVoiceBox";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider1 from "./Slider1";
import {
  Navigate,
  NavLink,
  Route,
  Routes,
  useNavigate,
} from "react-router-dom";
import Show from "./Show";
const reducer = (state, action) => {
  switch (action.type) {
    case "loader":
      return { ...state, loader: !state.loader };
    case "voicebox":
      return { ...state, voicebox: !state.voicebox };
    case "searchinp":
      return { ...state, searchinp: action.data };
    case "genres":
      return { ...state, genres: action.data };
    case "clear":
      return { ...state, searchinp: "" };
    case "msearch":
      return { ...state, msearch: !state.msearch };
    default:
      return { state };
  }
};
export default function App() {
  const navigate = useNavigate();
  const Api_key = "d5c35e51c81488b19da7c1f572507a3d";
  // -------------------------------------- Genre
  const send_req_2 = async () => {
    const req = await fetch(
      `https://api.themoviedb.org/3/genre/movie/list?api_key=${Api_key}&language=en-US`
    );
    const res = await req.json();
    dispatch({ type: "genres", data: res.genres });
  };
  // -------------------------------------- Genre

  const search = () => {
    if (state.searchinp.trim() != "") {
      navigate(`/Search/${state.searchinp}/1`);
      dispatch({ type: "clear" });
    } else {
      alert("type something");
    }
  };
  const search2 = (e) => {
    if (e.trim() != "") {
      navigate(`/Search/${e}/1`);
      dispatch({ type: "clear" });
    } else {
      alert("type something");
    }
  };
  const [state, dispatch] = useReducer(reducer, {
    loader: true,
    voicebox: false,
    genre: false,
    movies: false,
    searchinp: "",
    msearch: false,
  });
  const loader_func = () => {
    setTimeout(() => {
      dispatch({ type: "loader" });
    }, 2000);
  };
  useEffect(() => {
    loader_func();
    send_req_2();
  }, []);

  return (
    <>
      {state.voicebox && (
        <ModalVoiceBox
          search={(e) => {
            search2(e);
          }}
          close={() => dispatch({ type: "voicebox" })}
        />
      )}
      {state.loader && <Loader />}
      {!state.loader && (
        <div className="wrapper">
          <div className="nav">
            <h2>Movea</h2>
            <ul>
              <li>
                <NavLink to="/Movies/popular" className="li-style">
                  Popular
                </NavLink>
              </li>
              <li>
                <NavLink to="/Movies/upcoming" className="li-style">
                  Upcoming
                </NavLink>
              </li>
              <li>
                <NavLink to="/Movies/top_rated" className="li-style">
                  Top Rated
                </NavLink>
              </li>
            </ul>
            <div className="search-tools">
              <div className="search-inp">
                <input
                  type="text"
                  name="search"
                  value={state.searchinp}
                  onChange={(e) =>
                    dispatch({ type: "searchinp", data: e.target.value })
                  }
                  onKeyPress={(e) => {
                    if (e.key === "Enter") {
                      search();
                    }
                  }}
                  placeholder="type something"
                />
                <IconButton className="m-r-10" onClick={() => search()}>
                  <SearchIcon className="search-ico" />
                </IconButton>
                <IconButton onClick={() => dispatch({ type: "voicebox" })}>
                  <MicIcon className="search-ico" />
                </IconButton>
              </div>
              <div className="search-inp2">
                <IconButton
                  className="m-r-10"
                  onClick={() => dispatch({ type: "msearch" })}
                >
                  <SearchIcon className="search-ico" />
                </IconButton>
                <IconButton onClick={() => dispatch({ type: "voicebox" })}>
                  <MicIcon className="search-ico" />
                </IconButton>
              </div>
            </div>
            {state.msearch && (
              <div className="mobile-search">
                <input
                  placeholder="type something"
                  type="text"
                  value={state.searchinp}
                  onChange={(e) =>
                    dispatch({ type: "searchinp", data: e.target.value })
                  }
                  onKeyPress={(e) => {
                    if (e.key === "Enter") {
                      search();
                    }
                  }}
                />
                <IconButton
                  className="m-r-10"
                  onClick={() => {
                    search();
                    dispatch({ type: "msearch" });
                  }}
                >
                  <SearchIcon className="search-ico" />
                </IconButton>
              </div>
            )}
          </div>

          <div className="badges">
            <h3>Search by Genres: </h3>
            <Slider1 genres={state.genres ? state.genres : []} />
          </div>

          <div className="mobile">
            <NavLink to="/Movies/popular" className="mobile-link">
              Popular
            </NavLink>
            <NavLink to="/Movies/upcoming" className="mobile-link">
              Upcoming
            </NavLink>
            <NavLink to="/Movies/top_rated" className="mobile-link">
              Top Rated
            </NavLink>
          </div>

          <Routes>
            <Route
              path="/"
              element={<Navigate to="/Movies/popular/1" replace />}
            />
            <Route
              path="/Movies/popular"
              element={<Navigate to="/Movies/popular/1" replace />}
            />
            <Route
              path="/Movies/upcoming"
              element={<Navigate to="/Movies/upcoming/1" replace />}
            />

            <Route
              path="/Movies/top_rated"
              element={<Navigate to="/Movies/top_rated/1" replace />}
            />
            <Route path="/:movies/:type/:number" element={<Movies />} />
            <Route path="/:movies/:type/:number/:video_id" element={<Show />} />
          </Routes>
          <footer>
            <h1>Movea</h1>
            <p>Created by Mo'minov Xikmatilla</p>
            <p>Used Technologies:</p>
            <div className="tech">
              <p>React,Aos,Mui,TMDB Api</p>
            </div>
          </footer>
        </div>
      )}
    </>
  );
}
