import React, { Component } from "react";
import Slider from "react-slick";
import { NavLink } from "react-router-dom";
export default function Slider1({ genres }) {
  const settings = {
    className: "slider variable-width",
    dots: false,
    infinite: true,
    centerMode: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    variableWidth: true,
  };
  // console.log(genres);
  return (
    <div>
      <Slider {...settings}>
        {genres.map((e) => (
          <div className="badge" key={e.id}>
            <NavLink className="badge-link" to={"MoviesByType/Genres/1" + e.id}>
              {e.name}
            </NavLink>
          </div>
        ))}
      </Slider>
    </div>
  );
}
