import React, { Component, useState } from "react";
import Slider from "react-slick";
import CircularProgress from "@mui/material/CircularProgress";

export default function Slider2({ video_t }) {
  const [fakeload, setfakeload] = useState(true);
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };
  return (
    <div className="main-slider2">
      <div className="slider2">
        <Slider {...settings}>
          {video_t.map((e) => (
            <div className="slide-item">
              {fakeload && (
                <div className="fake-load-slider2">
                  <CircularProgress />
                </div>
              )}
              <iframe
                src={"https://www.youtube.com/embed/" + e.key}
                frameborder="0"
                onLoad={() => setfakeload(false)}
              ></iframe>
            </div>
          ))}
        </Slider>
      </div>
    </div>
  );
}
