import React, { useEffect, useState } from "react";
import { useParams, useLocation } from "react-router-dom";
import Slider1 from "./Slider1";
import noimg from "./noimg.jpg";
import Slider2 from "./Slider2";
import ArrowCircleLeftIcon from "@mui/icons-material/ArrowCircleLeft";
export default function Show() {
  // const location = useLocation();
  const Api_key = "d5c35e51c81488b19da7c1f572507a3d";
  const { video_id } = useParams();
  const [datas, setdatas] = useState(false);
  const send_req = async () => {
    const req = await fetch(
      `https://api.themoviedb.org/3/movie/${video_id}?api_key=${Api_key}&append_to_response=videos`
    );
    const res = await req.json();
    setdatas((prev) => (prev = { ...prev, data: res }));
  };

  useEffect(() => {
    send_req();
    setdatas({
      id: video_id,
      // genres: location.state.genres,
      // data: location.state.data,
    });
  }, []);
  return (
    <div className="show" data-aos="zoom-in">
      <div className="back-btn" onClick={() => window.history.back()}>
        <ArrowCircleLeftIcon />
        <p>Go back</p>
      </div>
      {datas.data && (
        <>
          <div className="for-big-screen">
            <div className="top-screen">
              <img
                className="backdrop"
                src={
                  datas.data.backdrop_path != null
                    ? "https://image.tmdb.org/t/p/original" +
                      datas.data.backdrop_path
                    : noimg
                }
              />
              <div className="infos">
                <img
                  className="info-img"
                  src={
                    datas.data.poster_path != null
                      ? "https://image.tmdb.org/t/p/w500" +
                        datas.data.poster_path
                      : noimg
                  }
                />
                <div>
                  <h1>{datas.data.title}</h1>
                  <p>
                    {datas.data.overview
                      ? datas.data.overview
                      : "no description"}
                  </p>
                  {/* <Slider1 genres={datas.data.genres ? datas.data.genres : []} /> */}
                  <div className="badges2">
                    <p className="vote">{datas.data.vote_average} 🔥</p>
                    {datas.data.genres &&
                      datas.data.genres.map((e) => (
                        <div className="badge2" key={datas.data.id}>
                          <p>{e.name}</p>
                        </div>
                      ))}
                  </div>
                </div>
              </div>
            </div>
            <div className="margin-top"></div>
            <h1 className="white2">
              {datas.data.videos.results.length <= 0 ? "No videos" : "Videos"}
            </h1>
            <Slider2
              video_t={
                datas.data.videos.results.length >= 2
                  ? datas.data.videos.results.slice(0, 2)
                  : datas.data.videos.results
              }
            />
          </div>
        </>
      )}
    </div>
  );
}
