import React, { useEffect, useState } from "react";
import {
  Navigate,
  useNavigate,
  useParams,
  Route,
  Routes,
} from "react-router-dom";
import Irouter from "./Irouter";
import svgloader from "./load.gif";
import Show from "./Show";
import noimg from "./noimg.jpg";
export default function Movies() {
  const { type, number, movies } = useParams();
  const [num, setnum] = useState(1);
  const [page, setpage] = useState(0);
  const [data, setdata] = useState(false);
  const [loader, setloader] = useState(false);
  const [videoid, setvideoid] = useState(false);

  const navigate = useNavigate();
  const update = (e) => {
    setnum(e);
  };
  const Api_key = "d5c35e51c81488b19da7c1f572507a3d";
  const send_req_3 = async (e, n) => {
    setloader(true);
    let req;
    switch (movies) {
      case "Movies":
        req = await fetch(
          `https://api.themoviedb.org/3/movie/${e}?api_key=${Api_key}&language=en-US&page=${n}`
        );
        break;
      case "MoviesByType":
        req = await fetch(
          `https://api.themoviedb.org/3/list/${number}?api_key=${Api_key}`
        );
        break;
      case "Search":
        req = await fetch(
          `https://api.themoviedb.org/3/search/movie?api_key=${Api_key}&language=en-US&query=${type}&page=${number}`
        );
        break;
    }
    const res = await req.json();
    switch (movies) {
      case "Movies":
        setdata(res.results);
        setpage(res.total_pages >= 500 ? 500 : res.total_pages);
        break;
      case "MoviesByType":
        if (res.items != undefined) {
          if (res.items.length <= 0) {
            setdata(undefined);
          } else {
            setdata(res.items);
          }
        } else {
          setdata(undefined);
        }
        setpage(0);
        break;
      case "Search":
        if (res.results != undefined) {
          if (res.results.length <= 0) {
            setdata(undefined);
            setpage(0);
          } else {
            setdata(res.results);
            setpage(res.total_pages >= 500 ? 500 : res.total_pages);
          }
        } else {
          setdata(undefined);
          setpage(0);
        }

        break;
    }
    window.scrollTo(0, 0);
    setloader(false);
  };

  useEffect(() => {
    setnum(Number(number));
  }, [number]);
  useEffect(() => {
    send_req_3(type, number);
  }, [number, type]);

  const show = (video_id, genres) => {
    navigate(`/${movies}/${type}/${number}/${video_id}`);
  };
  return (
    <div className="movies">
      <div className="boxes">
        {loader && (
          <div className="loader2">
            <img src={svgloader} alt="" />
            <h2>Loading...</h2>
          </div>
        )}
        {data ? (
          data.map((e) => (
            <div className="box" key={e.id}>
              <img
                src={
                  e.poster_path != null
                    ? "https://image.tmdb.org/t/p/w500" + e.poster_path
                    : noimg
                }
                alt=""
              />
              <div className="star">
                {e.vote_average && <p> {e.vote_average} 🔥</p>}
              </div>
              <div className="star2">
                {e.vote_average && <p> {e.release_date.slice(0, 4)}</p>}
              </div>

              {e.title && (
                <div className="info-box">
                  <p className="title">{e.title}</p>
                  <div
                    className="watch-btn"
                    onClick={() => show(e.id, e.genre_ids)}
                  >
                    Watch
                  </div>
                </div>
              )}
            </div>
          ))
        ) : (
          <h1 className="nodata">No Data</h1>
        )}
      </div>
      <div className="irouter">
        <Irouter
          pages={page}
          setnum={() => Number(num)}
          name={type}
          type={movies}
          update={(e) => {
            update(e);
          }}
        />
      </div>
    </div>
  );
}
